body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  height: 100%;
  min-width: 380px;
  font-family: Helvetica, Verdana, Arial, sans-serif;
}

@font-face {
  font-family: "Tungsten";
  src: url("./fonts/Tungsten-SemiBold.woff") format("woff"),
    url("./fonts/Tungsten-SemiBold.ttf") format("truetype");
}
